.threeQUpload-modal.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease;
}
.threeQUpload-modal .modal-wrapper {
  width: 400px;
  max-width: 90%;
  overflow: hidden;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
}
.threeQUpload-modal .modal-container {
  display: flex;
  flex-direction: column;
  max-height: 90vh;
  overflow: hidden;
}
.threeQUpload-modal .modal-container .modal-header {
  flex: none;
}
.threeQUpload-modal .modal-container .modal-body {
  flex: 1 1 auto;
  min-width: 0;
  min-height: 0;
  overflow: auto;
}
.threeQUpload-modal .modal-container .modal-footer {
  flex: none;
  padding: 20px;
}
.threeQUpload-modal .modal-header h3 {
  margin: 10px 0 !important;
  font-weight: bold;
}
.threeQUpload-modal.modal-enter {
  opacity: 0;
}
.threeQUpload-modal.modal-leave-active {
  opacity: 0;
}
.threeQUpload-modal.modal-enter .modal-wrapper,
.threeQUpload-modal.modal-leave-active .modal-wrapper {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
