
@import "~styles/base";
.threeQUpload .threeQUpload-downloadAction {
  .threeQUpload-downloadModal {
    .loader {
      font-size: 2rem;
    }

    ul {
      width: 100%;
      padding: 0;
      margin: 0;

      li {
        display: flex;
        align-items: center;
        padding: @space-2 @space-1;

        &:not(:last-child) {
          border-bottom: 1px solid @color-light-grey;
        }

        div.title {
          flex: 1 1 auto;
        }

        div.actions {
          --action-size: 25px;
          flex: none;
          display: flex;
          gap: @space-2;

          a {
            color: @color-mono-20;
            transition: color 200ms ease-in-out;
            width: var(--action-size);
            height: var(--action-size);
            line-height: var(--action-size);
            border: 1px solid;
            text-align: center;
            border-radius: 4px;

            &:hover {
              color: @color-blue-primary;
            }
          }
        }
      }
    }
  }
}
