
@import "~styles/base";

.threeQUpload {
  .threeQUpload-orSelectHint {
    margin: @space-2 0;
    text-align: center;
    font-size: 1.25rem;
    font-weight: bold;
  }

  .threeQUpload-keepFileHint {
    .btn {
      font-size: 1.15rem;
      font-weight: bold;
    }

    .threeQUpload-keepFileHint-filename {
      font-size: 0.75rem;
      font-weight: normal;
      max-width: 300px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
