
@import "~styles/base";

.threeQUpload .threeQUpload-uploader {
  margin-top: @space-2;

  .vue-dropzone {
    border: 2px dashed @color-border;
    border-radius: @border-radius;
    background: @color-mono-100;
    padding: @space-2;
    height: 68px;
    display: flex;
    justify-content: center;
    align-items: center;

    &.dz-clickable {
      cursor: pointer;
    }

    &.dz-drag-hover {
      box-shadow: 0 0 10px inset @color-border;
    }

    .threeQUpload-uploader-content {
      text-align: center;

      .threeQUpload-uploader-content-title {
        margin: 0;
        font-size: 1.15rem;
        font-weight: bold;
      }

      .threeQUpload-uploader-content-subTitle {
        font-size: 0.875rem;
      }
    }

    &.dz-started {
      .dz-message {
        display: none;
      }
    }

    .dz-preview {
      min-width: 300px;

      .dz-image, .dz-success-mark, .dz-error-mark {
        display: none;
      }
      .dz-details {
        display: flex;
        justify-content: center;

        .dz-size {
          margin-right: @space-2;
        }
      }
      .dz-progress {
        width: 100%;
        height: 8px;
        position: relative;
        margin-top: @space-1;
        background: @color-border;

        .dz-upload {
          position: absolute;
          top: 0;
          left: 0;
          background: @color-success;
          height: 100%;
        }
      }
    }
  }
}
