.threeQUpload .threeQUpload-uploader {
  margin-top: 10px;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone {
  border: 2px dashed #c1cad9;
  border-radius: 5px;
  background: #fff;
  padding: 10px;
  height: 68px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone.dz-clickable {
  cursor: pointer;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone.dz-drag-hover {
  box-shadow: 0 0 10px inset #c1cad9;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone .threeQUpload-uploader-content {
  text-align: center;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone .threeQUpload-uploader-content .threeQUpload-uploader-content-title {
  margin: 0;
  font-size: 1.15rem;
  font-weight: bold;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone .threeQUpload-uploader-content .threeQUpload-uploader-content-subTitle {
  font-size: 0.875rem;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone.dz-started .dz-message {
  display: none;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone .dz-preview {
  min-width: 300px;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone .dz-preview .dz-image,
.threeQUpload .threeQUpload-uploader .vue-dropzone .dz-preview .dz-success-mark,
.threeQUpload .threeQUpload-uploader .vue-dropzone .dz-preview .dz-error-mark {
  display: none;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone .dz-preview .dz-details {
  display: flex;
  justify-content: center;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone .dz-preview .dz-details .dz-size {
  margin-right: 10px;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone .dz-preview .dz-progress {
  width: 100%;
  height: 8px;
  position: relative;
  margin-top: 5px;
  background: #c1cad9;
}
.threeQUpload .threeQUpload-uploader .vue-dropzone .dz-preview .dz-progress .dz-upload {
  position: absolute;
  top: 0;
  left: 0;
  background: #65ac1e;
  height: 100%;
}
