.threeQUpload .threeQUpload-select {
  position: relative;
}
.threeQUpload .threeQUpload-select .threeQUpload-select-loadingIndicator {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1.25rem;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.threeQUpload .threeQUpload-select input[type=text] {
  display: block;
  width: 100%;
  height: 40px;
  padding: 10px;
  border: 1px solid #ced5e1;
  background: #fff;
  transition: all 250ms ease-in-out;
  outline: none;
  box-shadow: none;
  -webkit-appearance: none;
  border-radius: 5px;
  text-align: center;
}
.threeQUpload .threeQUpload-select input[type=text]:focus {
  border-color: #66afe9;
}
.threeQUpload .threeQUpload-select .vue-simple-suggest {
  position: relative;
}
.threeQUpload .threeQUpload-select .vue-simple-suggest > ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}
.threeQUpload .threeQUpload-select .vue-simple-suggest .suggestions {
  position: absolute;
  width: 100%;
  left: 0;
  top: 100%;
  background: rgba(255, 255, 255, 0.93);
  z-index: 1000;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.16);
  border: 1px solid #66afe9;
  border-top: 0;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.threeQUpload .threeQUpload-select .vue-simple-suggest .suggestions .suggest-item {
  cursor: pointer;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.threeQUpload .threeQUpload-select .vue-simple-suggest .suggestions .suggest-item.hover,
.threeQUpload .threeQUpload-select .vue-simple-suggest .suggestions .suggest-item.selected {
  background-color: #66afe9;
  color: #fff;
}
.threeQUpload .threeQUpload-select .vue-simple-suggest .suggestions .suggest-item,
.threeQUpload .threeQUpload-select .vue-simple-suggest .suggestions .misc-item {
  padding: 10px;
}
